import React from 'react';

interface CircularProgressViewProps {
  completedKits: number;
  totalKits: number;
  currentElapsedKitTime: number; // in seconds
  expectedKitDuration: number; // in seconds
  performanceStatus: 'AHEAD' | 'ON_TRACK' | 'BEHIND';
  isPaused: boolean;
  jobNumber: string;
  customerName: string;
  description: string;
}

const CircularProgressView: React.FC<CircularProgressViewProps> = ({
  completedKits,
  totalKits,
  currentElapsedKitTime,
  expectedKitDuration,
  performanceStatus,
  isPaused,
  jobNumber,
  customerName,
  description
}) => {
  // SVG dimensions
  const size = 800;
  const center = size / 2;

  // Ring specifications
  const outerRadius = 350;
  const outerStrokeWidth = 60;
  const innerRadius = 220;

  // Calculate circumferences
  const outerCircumference = 2 * Math.PI * outerRadius;
  const innerCircumference = 2 * Math.PI * innerRadius;

  // Calculate job progress (inner pie)
  const jobProgressPercent = totalKits > 0 ? (completedKits / totalKits) : 0;
  const jobProgressOffset = innerCircumference * (1 - jobProgressPercent);

  // Calculate kit countdown (outer doughnut)
  // Remaining time as percentage - counts DOWN from 100% to 0%
  const remainingKitTime = Math.max(0, expectedKitDuration - currentElapsedKitTime);
  const kitProgressPercent = expectedKitDuration > 0 ? (remainingKitTime / expectedKitDuration) : 1;
  const kitProgressOffset = outerCircumference * (1 - kitProgressPercent);

  // Determine if kit is overdue
  const isOverdue = currentElapsedKitTime > expectedKitDuration;

  // Color selection based on performance
  const getKitColor = () => {
    if (isOverdue) return '#dc2626'; // red-600
    if (performanceStatus === 'AHEAD') return '#16a34a'; // green-600
    if (performanceStatus === 'BEHIND') return '#ea580c'; // orange-600
    return '#2563eb'; // blue-600 (ON_TRACK)
  };

  const getJobColor = () => {
    const progressPercent = (completedKits / totalKits) * 100;
    if (progressPercent >= 75) return '#16a34a'; // green-600
    if (progressPercent >= 50) return '#2563eb'; // blue-600
    if (progressPercent >= 25) return '#f59e0b'; // amber-500
    return '#6b7280'; // gray-500
  };

  const kitColor = getKitColor();
  const jobColor = getJobColor();

  // Format time display
  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-[600px] bg-white rounded-lg shadow-2xl p-8">
      {/* Job Info Header */}
      <div className="text-center mb-6">
        <h1 className="text-3xl font-bold text-gray-800 mb-2">
          {jobNumber} - {description}
        </h1>
        <p className="text-lg text-gray-600">{customerName}</p>
      </div>

      {/* Circular Progress Visualization */}
      <div className="relative" style={{ width: size, height: size }}>
        <svg
          width={size}
          height={size}
          viewBox={`0 0 ${size} ${size}`}
          className="transform -rotate-90"
        >
          {/* Background circles */}
          <circle
            cx={center}
            cy={center}
            r={outerRadius}
            fill="none"
            stroke="#e5e7eb"
            strokeWidth={outerStrokeWidth}
          />
          <circle
            cx={center}
            cy={center}
            r={innerRadius}
            fill="none"
            stroke="#f3f4f6"
            strokeWidth={innerRadius * 2}
          />

          {/* Inner Pie Chart - Job Progress */}
          <circle
            cx={center}
            cy={center}
            r={innerRadius}
            fill="none"
            stroke={jobColor}
            strokeWidth={innerRadius * 2}
            strokeDasharray={innerCircumference}
            strokeDashoffset={jobProgressOffset}
            className="transition-all duration-500 ease-in-out"
            style={{
              transformOrigin: 'center',
            }}
          />

          {/* Outer Doughnut - Kit Countdown */}
          <circle
            cx={center}
            cy={center}
            r={outerRadius}
            fill="none"
            stroke={kitColor}
            strokeWidth={outerStrokeWidth}
            strokeDasharray={outerCircumference}
            strokeDashoffset={kitProgressOffset}
            strokeLinecap="round"
            className={`transition-all duration-1000 ease-linear ${isOverdue ? 'animate-pulse' : ''}`}
            style={{
              transformOrigin: 'center',
            }}
          />
        </svg>

        {/* Center Text Overlay */}
        <div className="absolute inset-0 flex flex-col items-center justify-center">
          <div className="text-center">
            {/* Kit Count */}
            <div className="mb-8">
              <div className="text-8xl font-bold text-gray-800 mb-2">
                {completedKits}<span className="text-gray-400">/{totalKits}</span>
              </div>
              <div className="text-2xl text-gray-600 font-semibold">Kits Completed</div>
            </div>

            {/* Kit Timer */}
            <div className="mb-4">
              <div className={`text-6xl font-mono font-bold mb-2 ${
                isOverdue ? 'text-red-600 animate-pulse' : 'text-blue-600'
              }`}>
                {formatTime(remainingKitTime)}
              </div>
              <div className="text-xl text-gray-600">
                {isOverdue ? 'OVERDUE!' : 'Kit Time Remaining'}
              </div>
            </div>

            {/* Performance Badge */}
            <div className={`inline-block px-6 py-3 rounded-full text-xl font-bold ${
              performanceStatus === 'AHEAD' ? 'bg-green-100 text-green-800' :
              performanceStatus === 'BEHIND' ? 'bg-red-100 text-red-800' :
              'bg-blue-100 text-blue-800'
            }`}>
              {performanceStatus.replace('_', ' ')}
            </div>

            {isPaused && (
              <div className="mt-4 px-6 py-2 bg-yellow-100 text-yellow-800 rounded-full text-lg font-semibold">
                 PAUSED
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Stats Bar */}
      <div className="mt-8 grid grid-cols-3 gap-8 w-full max-w-2xl">
        <div className="text-center">
          <div className="text-3xl font-bold text-purple-600">
            {formatTime(currentElapsedKitTime)}
          </div>
          <div className="text-sm text-gray-600 mt-1">Current Kit Time</div>
        </div>

        <div className="text-center">
          <div className="text-3xl font-bold text-blue-600">
            {formatTime(expectedKitDuration)}
          </div>
          <div className="text-sm text-gray-600 mt-1">Expected Kit Time</div>
        </div>

        <div className="text-center">
          <div className={`text-3xl font-bold ${
            isOverdue ? 'text-red-600' : 'text-green-600'
          }`}>
            {isOverdue ? '+' : ''}{formatTime(Math.abs(currentElapsedKitTime - expectedKitDuration))}
          </div>
          <div className="text-sm text-gray-600 mt-1">
            {isOverdue ? 'Overtime' : 'Time Savings'}
          </div>
        </div>
      </div>

      {/* Progress Percentage */}
      <div className="mt-6 text-center">
        <div className="text-4xl font-bold text-gray-700">
          {Math.round(jobProgressPercent * 100)}%
        </div>
        <div className="text-lg text-gray-600">Job Complete</div>
      </div>
    </div>
  );
};

export default CircularProgressView;
